@import url(https://dev-cats.github.io/code-snippets/JetBrainsMono.css);
:root {
  --background-color: #07051f;
  --black-white: rgba(255, 255, 255, .6)
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "JetBrains Mono", sans-serif;
  color: white;
  padding: 0;
  margin: 0;
}

html {
  font-size: 0.9vw;
  overflow-x: hidden;
  background: #07051f;
  background: var(--background-color);
}

section {
  width: 100vw;
  height: 100vh;
}


section:not(:first-child) > div:last-child {
  width: 80%;
  height: 90%;
}


p {
  font-family: "JetBrains Mono", sans-serif;
}

html {
  overflow-x: hidden;
}

.home {
    height: 100vh;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
}

.home > * {
    flex: 1 0;
    background: var(--background-color);
    height: inherit;
    font-family: "JetBrains Mono", sans-serif;
    overflow: hidden;
}

.home > .name {
    display: flex;
    align-items: stretch;
    flex-direction: column;
    text-align: left;
}

.name > p:first-child {
    margin-top: 26vh;
}

.home p {
    width: 60%;
    position: relative;
    left: 5vw;
    font-size: 3.2rem;
    color: white;
    display: flex;
    flex-direction: column;
}

.name-p {
    padding-bottom: 2vh;
}

.name-p ~ p, .name > p:first-child:before {
    font-size: 2rem;
    color: rgba(255, 255, 255, 0.6);
}

.name > p:first-child:before {
    content: "// hello world";
    font-size: .9rem;
}

.home > .menu {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}

#menu ul {
    position: fixed;
    right: 8%;
    top: 26%;
}

.picture {
    width: 100%;
    height: 80%;
    object-fit: cover;
    opacity: 1;
    position: relative;
    left: 12.5%;
}

.picture-code {
    position: relative;
    height: 18%;
    width: 40%;
    bottom: 10%;
    object-fit: scale-down;
}

.pics {
    height: 100%;
    width: 60%;
}

.picture,
.picture-code {
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

#menu {
    width: 40%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-left: 12%;
}

.menu-element {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    color: rgba(255, 255, 255, .6);
    font-size: 1.4rem;
    transition: all 200ms;
}

.menu-element:not(:first-child) {
    padding: .8vh 0;
}

.menu-element > p {
    color: rgba(255, 255, 255, .6);
    font-size: 1.4rem;
    transition: all 400ms;
    transform-origin: 70% 50%;
}

.menu-element:hover {
    text-decoration: line-through;
    color: white;
}



#portfolio {
    display: inline-flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
}

#portfolio > h1 {
    flex: 1 1;
}

#portfolio > #projects {
    flex: 20 1;
}


#projects {
    width: 100%;
    display: flex;
    min-height: 70vh;
}

#projects > .content {
    width: 90%;
    height: 60%;
    margin-left: 17vh;
}

.type-element {
    display: inline-block;
    color: var(--black-white);
    margin-right: 4%;
    transition: all 200ms;
    font-size: 1.4rem;
}

.type-element:hover {
    color: white;
}

.projects-grid {
    margin-top: 2%;
    width: 100%;
    display: inline-flex;
    flex-direction: row;
}

.projects-grid > .projects-name {
    display: inline;
    flex: 2 1;
}

.projects-grid .info {
    flex: 1.2 1;
}

.content {
    display: inline-flex;
    flex-direction: column;
}

.content > ul {
    flex: 1 1;
}

.content > .projects-grid {
    flex: 20 1;
}

.card {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    padding: 2% 0;
}

.card > p {
    color: var(--black-white);
    transition: all 100ms;
}

.title {
    color: var(--black-white);
    font-size: 2.1rem;
    margin-bottom: 1%;
}

.card > p:last-child {
    font-size: 1.4rem;
}

.card:hover > p {
    color: white;
}

.card.colored > * {
    color: white;
}






.info {
    overflow: hidden;
    display: inline-block;
    height: 100%;
}

.info > .projects-image {
    width: 100%;
    height: 40%;
    margin-bottom: 2vh;
    /*
    animation: animate-image 400s linear infinite;
    */
}

.info p {
    color: var(--black-white);
}

.printing > p.title {
    color: white;
    font-size: 2rem;
}

.info > .content {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
}

.content > .description {
    flex: 7 1;
}

.content > .stack {
    flex: 2 1;
    height: 100%;
    font-size: .8rem;
}

@keyframes animate-image {
    0% {
        transform: translate3d(0, 0, 0);
    }
    50% {
        transform: translate3d(-100%, 0, 0);
    }
    100% {
        transform: translate3d(0, 0, 0);
    }
}


.printing > * {
    display: inline-block;
}

.printing {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.printing > * {
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.cursor {
    width: 5px;
    height: 2rem;
    background: white;
    margin-left: 10px;
}

.blinking {
    animation: cursor-blink 1s forwards infinite;
}

@keyframes cursor-blink {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

div[id$="before"] {
}

h1.global-title {
    margin: 2vh 0;
    font-size: 2rem;
    position: relative;
    left: 5vw;
    bottom: 2vh;
}


.experience {
    width: 80%;
    height: 80vh !important;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.experience > div {
    height: 100%;
    width: 100%;
    flex: 1 1;
}

.experience > div > *:nth-child(2) {
    margin-top: 20px;
}

.experience .experience-card  {
    position: relative;
    left: 5vw;
    bottom: 2vh;
    padding: 10px 15px 0  ;
}


.experience-card * {
    display: inline-block;
    margin: 0 5px;
}

.text-with-borders::after {
    content: "//";
    color: #a7c5eb;
    margin: 0 2px;
}

.text-with-borders {
    margin-left: 40px;
}

.experience-card h1 {
    font-size: 1.7rem;
}

.contact {
    height: 40vh;
    min-height: 40vh !important;
}

.contact > .content {
    padding: 25px;
    height: 100%;
    width: 100% !important;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.write {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    flex: 1 1;
}

.contacts {
    height: 100%;
    flex: 1 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.contacts > div {
    width: 50%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.write > form {
    width: 40%;
    height: 60%;
    grid-gap: 3%;
    gap: 3%;
    display: flex;
    flex-direction: column;
}

.center {
    display: flex;
}

.contact-title {
    color: var(--black-white)
}

.contacts p {
    font-size: .9rem;
}

.contacts h1 {
    font-size: 1rem;
    padding-bottom: 2vh;
}

input[type="text"], textarea {
    background: var(--background-color);
    border: 2px white solid;
    padding: 10px;
    font-size: 0.9rem;
}

#message {
    min-height: 30%;
}

#send {
    color: white;
    background: var(--background-color);
    min-height: 20%;
    border: 2px white solid;
    font-size: 0.9rem;
}

